<!-- 模板 -->
<template>
    <div id="add-blog">
        <h2>编辑博客</h2>
        <form v-if="!summited">
            <label>博客标题</label>
            <input type="text" v-model="blog.title" required />
            <label>博客正文</label>
            <textarea v-model="blog.content"></textarea>
            <!-- <div class="content">
                                <div class="info">UE编辑器示例<br>需要使用编辑器时，调用UE公共组件即可。可设置填充内容content，配置信息config(宽度和高度等)，可调用组件中获取内容的方法。支持页面内多次调用。
                                </div>
                                <button @click="getUEContent()">获取内容</button>
                                <ueditor :content=content1 :config=config1 :id="ue1"></ueditor>
                            </div> -->
            <!-- <div id="editor"></div> -->
            <div id="checkboxes">
                <label>Vue.js</label>
                <input type="checkbox" value="Vue.js" v-model="blog.categories">
                <label>Node.js</label>
                <input type="checkbox" value="Node.js" v-model="blog.categories">
                <label>React.js</label>
                <input type="checkbox" value="React.js" v-model="blog.categories">
                <label>Angular4</label>
                <input type="checkbox" value="Angular4" v-model="blog.categories">
                <label>杂项</label>
                <input type="checkbox" value="杂项" v-model="blog.categories">
            </div>
            <label>发布者</label>
            <select v-model="blog.author" class="author">
                <option v-for="author in authors">
                    {{author}}
                </option>
            </select>
            <button v-on:click.prevent="post">完成编辑</button>
        </form>
        <div v-if="summited">
            <h3>您的博客发送成功</h3>
        </div>
        <hr>
        <div id="preview">
            <h3>博客预览</h3>
            <p>博客标题:{{blog.title}}</p>
            <p>博客内容:</p>
            <p class="textarea2">{{blog.content}}</p>
            <p>博客分类:</p>
            <ul>
                <li v-for="category in blog.categories" :key="category">
                    {{category}}
                </li>
            </ul>
            <p>发布者:{{blog.author}}</p>
        </div>
    </div>
</template>
<!-- Script -->

<script>
    import WangEditor from 'wangeditor'
    let that = this
    this.editor = new WangEditor('#WangEditor') //这个地方传入div元素的id 需要加#号
    // 配置 onchange 事件
    this.editor.change = function() { // 这里是change 不是官方文档中的 onchange
        // 编辑区域内容变化时，实时打印出当前内容
        console.log(this.txt.html())
    }
    this.editor.create() // 生成编辑器
    this.editor.txt.html('<p>输入内容...</p>') //注意：这个地方是txt  不是官方文档中的$txt
    export default {
        name: 'add-blog',
        data() {
            return {
                id: this.$route.params.id,
                blog: {
                    title: "",
                    content: "",
                    categories: [],
                    author: ""
                },
                authors: ["Caico", "Jaime", "Messi"],
                summited: false //v-if是根据表达式的值判断一个元素是生成或移除，如果判断为true，就生成元素；否则，移除元素。
            }
        },
        created() {
            this.fetchData();
        },
        methods: {
            fetchData() {
                this.$http.get("https://my-vue-data-base1.firebaseio.com/post/" + this.id + ".json")
                    .then(response => {
                        this.blog = response.body;
                    })
            },
            post: function() {
                this.$http.put("https://my-vue-data-base1.firebaseio.com/post/" + this.id + ".json", this.blog)
                    .then(data => console.log(data));
                this.summited = true;
            }
        }
    }
</script>

<style scoped>
    .w-e-menu {
        z-index: 2 !important;
    }
    .w-e-text-container {
        z-index: 1 !important;
    }
    #add-blog * {
        box-sizing: border-box;
        text-align: center;
    }
    #add-blog {
        margin: 20px auto;
        max-width: 600px;
        padding: 20px;
    }
    label {
        display: block;
        margin: 20px 0 10px;
    }
    input[type="text"],
    textarea,
    select {
        display: block;
        width: 100%;
        padding: 8px;
    }
    textarea {
        min-width: 600px;
        max-width: 600px;
        min-height: 220px;
        border: 1px solid #444;
    }
    textarea,
    .textarea2 {
        text-align: left !important;
        text-indent: 2em;
    }
    ul li {
        list-style-type: none;
        display: inline-block;
        margin: 0 5px;
    }
    #checkboxes label {
        display: inline-block;
        margin-top: 0;
    }
    #checkboxes input {
        display: inline-block;
        padding: 0 5px;
    }
    select.author {
        width: 100px;
        margin: 0 auto;
    }
    button {
        display: block;
        margin: 20px auto;
        background: crimson;
        color: #fff;
        border: 0;
        padding: 14px;
        border-radius: 4px;
        font-size: 18px;
        cursor: pointer;
    }
    #preview {
        padding: 10px 20px;
        border: 1px dotted #ccc;
        margin: 30px 0;
    }
    h3 {
        margin-top: 10px;
    }
</style>



